import React, { Component } from 'react'
import counterpart from 'counterpart';
import Translate from 'react-translate-component'; // displays registered translations

counterpart.setLocale('{this.props.language}');


export default class Edukacja extends Component {
  render() {
    return (
            <section className="colorlib-education" data-section="education">
              <div className="colorlib-narrow-content">
                <div className="row">
                  <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                  <Translate content="education.h2" component="h2" className="colorlib-heading" unsafe={true}></Translate>

                  </div>
                </div>
                <div className="row">
                  <div className="col-md-12 animate-box" data-animate-effect="fadeInLeft">
                    <div className="fancy-collapse-panel">
                      <div className="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
                        <div className="panel panel-default">
                          <div className="panel-heading" role="tab" id="headingOne">
                            <h4 className="panel-title">
                              <a data-toggle="collapse" data-parent="#accordion" href="#collapseOne" aria-expanded="true" aria-controls="collapseOne"> <Translate content="education.p1" component="p"  unsafe={true}></Translate>
                              </a>
                            </h4>
                          </div>
                          <div id="collapseOne" className="panel-collapse collapse in" role="tabpanel" aria-labelledby="headingOne">
                            <div className="panel-body">
                              <div className="row">
                                <div className="col-md-6">
                                <Translate content="education.p2" component="p"  unsafe={true}></Translate>
                                  </div>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="panel panel-default">
                          <div className="panel-heading" role="tab" id="headingTwo">
                            <h4 className="panel-title">
                              <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseTwo" aria-expanded="false" aria-controls="collapseTwo"><Translate content="education.p3" component="p"  unsafe={true}></Translate>
                              </a>
                            </h4>
                          </div>
                          <div id="collapseTwo" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingTwo">
                            <div className="panel-body">
                            <Translate content="education.p4" component="p"  unsafe={true}></Translate>
                            	
                            </div>
                          </div>
                        </div>
                        <div className="panel panel-default">
                          <div className="panel-heading" role="tab" id="headingThree">
                            <h4 className="panel-title">
                              <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseThree" aria-expanded="false" aria-controls="collapseThree"><Translate content="education.p5" component="p"  unsafe={true}></Translate>
                              </a>
                            </h4>
                          </div>
                          <div id="collapseThree" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingThree">
                            <div className="panel-body">
                            <Translate content="education.p6" component="p"  unsafe={true}></Translate>
                            
                            </div>
                          </div>
                        </div>
                        <div className="panel panel-default">
                          <div className="panel-heading" role="tab" id="headingFour">
                            <h4 className="panel-title">
                              <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseFour" aria-expanded="false" aria-controls="collapseFour"><Translate content="education.p7" component="p"  unsafe={true}></Translate>
                              </a>
                            </h4>
                          </div>
                          <div id="collapseFour" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingFour">
                            <div className="panel-body">
                            <Translate content="education.p8" component="p"  unsafe={true}></Translate>
                              
                            </div>
                          </div>
                        </div>
                        <div className="panel panel-default">
                          <div className="panel-heading" role="tab" id="headingFive">
                            <h4 className="panel-title">
                              <a className="collapsed" data-toggle="collapse" data-parent="#accordion" href="#collapseFive" aria-expanded="false" aria-controls="collapseFive">Szkoła ponadpodstawowa
                              </a>
                            </h4>
                          </div>
                          <div id="collapseFive" className="panel-collapse collapse" role="tabpanel" aria-labelledby="headingFive">
                            <div className="panel-body">
                              <p>II Liceum Ogólnokształcące im. Bronisława Taraszkiewicza w Bielsku Podlaskim</p>	
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </section>
          );
        }
      };