import React, { Component } from 'react'
import Introduction from './intro'
import About from './about'
import Edukacja from './edukacja'
import Sidebar from './sidebar'
import Skills from './skills'
import Projects from './projects'
import Timeline from './timeline'
import counterpart from 'counterpart';
import pl from '../lang/pl.js'
import en from '../lang/en.js'

counterpart.registerTranslations('pl', pl 
);
counterpart.registerTranslations('en', en
);
counterpart.setLocale('pl');

export default class Switcher extends Component {
  constructor(props) {
    super(props);
    this.state = {lang: 'pl'};
  }
  onLangChange = (e) => {
    this.setState({lang: e.target.value});
    counterpart.setLocale(e.target.value);
  }
  render() {
    return (
      <div>
      <div>
      <select value={this.state.lang} onChange={this.onLangChange}>
                    <option value="en">English version</option>
                    <option value="pl">Wersja polska</option>

                  </select>
                  </div>
                  <Introduction language={this.state.lang} />;  
                  <About language={this.state.lang} />;   
                  <Skills language={this.state.lang} />;  
                  <Timeline language={this.state.lang} />; 
                  <Edukacja language={this.state.lang} />;  
                  <Projects language={this.state.lang} />;  
      </div>
    )
  }
}