import React, { Component } from 'react'
import counterpart from 'counterpart';
import Translate from 'react-translate-component'; // displays registered translations

counterpart.setLocale('{this.props.language}');


export default class Timeline extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="timeline">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
               
              <Translate content="timeline.h2" component="h2" className="colorlib-heading" unsafe={true}></Translate>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-2">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                      <Translate content="timeline.h21" component="h2"  unsafe={true}></Translate>
                      <Translate content="timeline.p1" component="p"  unsafe={true}></Translate>
               
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-2">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                      <Translate content="timeline.h22" component="h2"  unsafe={true}></Translate>
                      <Translate content="timeline.p2" component="p"  unsafe={true}></Translate>
               
                        <h2></h2>
                        <p> </p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-2">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                      <Translate content="timeline.h23" component="h2"  unsafe={true}></Translate>
                      <Translate content="timeline.p3" component="p"  unsafe={true}></Translate>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}